
.ItemCard__main-header-link:Hover{
    color: rgba(255,105,180,0.8);
  }
  .projects__main {
    width: 60%;
    
    margin-top: 4rem;
    transform: translateX(30%);

  }
  .ItemCard__main:hover{
    transform: translateY(-2%);
    box-shadow: 0px 0px 10px rgba(255,105,180,0.8);
  }

.projects__main-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 1rem;
    margin-top: 1rem;
    justify-items: center;
    position: relative;
   
}
.ItemCard__main {
    width: 250px;
    height: 250px;
    border-radius: 10px;
    margin-right: 0rem;

}
footer {
    display: block;
}

.ItemCard__main-content-list {
padding: 0px;
margin-top: 30px;

}
.ItemCard__main-content-text {
    height: 100px;
}
